<script>
export default {
  name: 'index',
  data () {
    return {
      currentSwiperIndex: 0,
      vedioStart: false
    };
  },
  methods: {
    swiperAdd () {
      if (this.currentSwiperIndex > 0) {
        this.currentSwiperIndex = 0;
      } else {
        this.currentSwiperIndex++;
      }
    },
    prevBtn () {
      clearInterval(window.time);
      if (this.currentSwiperIndex < 1) {
        this.currentSwiperIndex = 1;
      } else {
        this.currentSwiperIndex--;
      }
      window.time = setInterval(() => {
        this.swiperAdd()
      }, 3000)
    },
    nextBtn () {
      clearInterval(window.time);
      if (this.currentSwiperIndex > 0) {
        this.currentSwiperIndex = 0;
      } else {
        this.currentSwiperIndex++;
      }
      window.time = setInterval(() => {
        this.swiperAdd()
      }, 3000)
    },
    vedioStartFunc () {
      this.vedioStart = true
    },
    vedioEndFunc (e) {
      console.log(e);
      if(e.target.tagName !== 'VIDEO') {
        this.vedioStart = false
      }
    }
  },
  mounted () {
    // window.time = setInterval(() => {
    //   this.swiperAdd()
    // }, 3000)
  }
}
</script>

